import * as z from "zod";

/**
 * Schema ÿßÿπÿ™ÿ®ÿßÿ±ÿ≥ŸÜÿ¨€å ŸÅÿ±ŸÖ Checkout
 */
export const checkoutFormSchema = z.object({
  firstName: z.string().min(1, "ŸÜÿßŸÖ ÿßŸÑÿ≤ÿßŸÖ€å ÿßÿ≥ÿ™"),
  lastName: z.string().optional(), // ŸÜÿßŸÖ ÿÆÿßŸÜŸàÿßÿØ⁄Ø€å ÿßÿÆÿ™€åÿßÿ±€å ÿßŸÖÿß Ÿæ€åÿ¥ŸÜŸáÿßÿØ€å
  phone: z
    .string()
    .min(10, "ÿ¥ŸÖÿßÿ±Ÿá ÿ™ŸÑŸÅŸÜ ÿ®ÿß€åÿØ ÿ≠ÿØÿßŸÇŸÑ 10 ÿ±ŸÇŸÖ ÿ®ÿßÿ¥ÿØ")
    .regex(/^[0-9]+$/, "ÿ¥ŸÖÿßÿ±Ÿá ÿ™ŸÑŸÅŸÜ ÿ®ÿß€åÿØ ŸÅŸÇÿ∑ ÿπÿØÿØ ÿ®ÿßÿ¥ÿØ"),
  email: z
    .union([z.string().email("ÿß€åŸÖ€åŸÑ ŸÜÿßŸÖÿπÿ™ÿ®ÿ± ÿßÿ≥ÿ™"), z.literal("")])
    .optional(), // ÿß€åŸÖ€åŸÑ ÿßÿÆÿ™€åÿßÿ±€å
  addressType: z.enum(["location", "postalCode", "address"], {
    required_error: "ŸÜŸàÿπ ÿ¢ÿØÿ±ÿ≥ ÿ±ÿß ÿßŸÜÿ™ÿÆÿßÿ® ⁄©ŸÜ€åÿØ",
  }),
  location: z.string().optional(), // ŸÑŸà⁄©€åÿ¥ŸÜ (ÿßÿÆÿ™€åÿßÿ±€å)
  address: z.string().optional(), // ÿ¢ÿØÿ±ÿ≥ ⁄©ÿßŸÖŸÑ (ÿßÿÆÿ™€åÿßÿ±€å)
  city: z.string().optional(), // ÿ¥Ÿáÿ± (ÿßÿÆÿ™€åÿßÿ±€å)
  postalCode: z
    .string()
    .optional()
    .refine((val) => !val || val === "" || /^[0-9]{10}$/.test(val), {
      message: "⁄©ÿØ Ÿæÿ≥ÿ™€å ÿ®ÿß€åÿØ 10 ÿ±ŸÇŸÖ ÿ®ÿßÿ¥ÿØ",
    }),
  province: z.string().optional(),
  notes: z
    .string()
    .max(500, "€åÿßÿØÿØÿßÿ¥ÿ™ ŸÜŸÖ€å‚Äåÿ™ŸàÿßŸÜÿØ ÿ®€åÿ¥ÿ™ÿ± ÿßÿ≤ 500 ⁄©ÿßÿ±ÿß⁄©ÿ™ÿ± ÿ®ÿßÿ¥ÿØ")
    .optional(),
}).superRefine((data, ctx) => {
  // ÿß⁄Øÿ± ŸÜŸàÿπ ÿ¢ÿØÿ±ÿ≥ location ÿßÿ≥ÿ™ÿå location ÿ®ÿß€åÿØ Ÿæÿ± ÿ®ÿßÿ¥ÿØ
  if (data.addressType === "location") {
    if (!data.location || data.location.trim().length === 0) {
      ctx.addIssue({
        code: z.ZodIssueCode.custom,
        message: "ŸÑÿ∑ŸÅÿßŸã ŸÑŸà⁄©€åÿ¥ŸÜ ÿ±ÿß Ÿàÿßÿ±ÿØ ⁄©ŸÜ€åÿØ",
        path: ["location"],
      });
    }
  }
  // ÿß⁄Øÿ± ŸÜŸàÿπ ÿ¢ÿØÿ±ÿ≥ postalCode ÿßÿ≥ÿ™ÿå postalCode ÿ®ÿß€åÿØ Ÿæÿ± ÿ®ÿßÿ¥ÿØ
  if (data.addressType === "postalCode") {
    if (!data.postalCode || data.postalCode.trim().length === 0) {
      ctx.addIssue({
        code: z.ZodIssueCode.custom,
        message: "ŸÑÿ∑ŸÅÿßŸã ⁄©ÿØ Ÿæÿ≥ÿ™€å ÿ±ÿß Ÿàÿßÿ±ÿØ ⁄©ŸÜ€åÿØ",
        path: ["postalCode"],
      });
    } else if (!/^[0-9]{10}$/.test(data.postalCode)) {
      ctx.addIssue({
        code: z.ZodIssueCode.custom,
        message: "⁄©ÿØ Ÿæÿ≥ÿ™€å ÿ®ÿß€åÿØ 10 ÿ±ŸÇŸÖ ÿ®ÿßÿ¥ÿØ",
        path: ["postalCode"],
      });
    }
  }
  // ÿß⁄Øÿ± ŸÜŸàÿπ ÿ¢ÿØÿ±ÿ≥ address ÿßÿ≥ÿ™ÿå address ÿ®ÿß€åÿØ Ÿæÿ± ÿ®ÿßÿ¥ÿØ
  if (data.addressType === "address") {
    if (!data.address || data.address.trim().length < 5) {
      ctx.addIssue({
        code: z.ZodIssueCode.custom,
        message: "ÿ¢ÿØÿ±ÿ≥ ÿ®ÿß€åÿØ ÿ≠ÿØÿßŸÇŸÑ 5 ⁄©ÿßÿ±ÿß⁄©ÿ™ÿ± ÿ®ÿßÿ¥ÿØ",
        path: ["address"],
      });
    }
  }
});

export type CheckoutFormData = z.infer<typeof checkoutFormSchema>;

/**
 * Sanitize Ÿà Ÿæÿß⁄©ÿ≥ÿßÿ≤€å ÿØÿßÿØŸá‚ÄåŸáÿß€å ŸÅÿ±ŸÖ
 */
export function sanitizeCheckoutInput(data: CheckoutFormData): CheckoutFormData {
  return {
    firstName: data.firstName.trim(),
    lastName: data.lastName?.trim() || "",
    phone: data.phone.replace(/\D/g, ""),
    email: data.email?.trim().toLowerCase() || "",
    addressType: data.addressType,
    location: data.location?.trim() || "",
    address: data.address?.trim() || "",
    city: data.city?.trim() || "",
    postalCode: data.postalCode?.replace(/\D/g, "") || "",
    province: data.province?.trim() || "",
    notes: data.notes?.trim() || "",
  };
}
