import { NextRequest } from "next/server";
import { checkoutFormSchema } from "@/lib/validations/checkout";
import { createErrorResponse, createSuccessResponse } from "@/lib/api-route-helpers";
import { AppError } from "@/lib/api-error-handler";
import { runQuery, getRow } from "@/lib/db/index";
import { logger } from "@/lib/logger";
import { getSessionUserFromRequest } from "@/lib/auth/session";

/**
 * Sanitize Ÿà ÿßÿπÿ™ÿ®ÿßÿ±ÿ≥ŸÜÿ¨€å Ÿàÿ±ŸàÿØ€å‚ÄåŸáÿß
 */
function sanitizeString(value: any): string {
  if (typeof value !== "string") return "";
  return value.trim();
}

function sanitizeNumber(value: any): number {
  const num = typeof value === "number" ? value : parseFloat(value);
  return isNaN(num) ? 0 : num;
}

export async function POST(request: NextRequest) {
  try {
    // Ensure orders table exists
    try {
      await runQuery(`
        CREATE TABLE IF NOT EXISTS orders (
          id VARCHAR(255) PRIMARY KEY,
          \`orderNumber\` VARCHAR(255) UNIQUE NOT NULL,
          \`userId\` VARCHAR(255),
          \`customerName\` VARCHAR(255) NOT NULL,
          \`customerPhone\` VARCHAR(255) NOT NULL,
          \`customerEmail\` VARCHAR(255),
          items JSON NOT NULL DEFAULT ('[]'),
          total BIGINT NOT NULL,
          \`shippingCost\` BIGINT NOT NULL,
          \`shippingMethod\` VARCHAR(50) NOT NULL,
          \`shippingAddress\` JSON NOT NULL DEFAULT ('{}'),
          status VARCHAR(50) NOT NULL DEFAULT 'pending',
          \`paymentStatus\` VARCHAR(50) NOT NULL DEFAULT 'pending',
          notes TEXT,
          \`createdAt\` TIMESTAMP NOT NULL DEFAULT CURRENT_TIMESTAMP,
          \`updatedAt\` TIMESTAMP NOT NULL DEFAULT CURRENT_TIMESTAMP ON UPDATE CURRENT_TIMESTAMP
        ) ENGINE=InnoDB DEFAULT CHARSET=utf8mb4 COLLATE=utf8mb4_unicode_ci
      `);
      logger.debug("Orders table ensured");
    } catch (tableError: any) {
      // Log but don't fail - table might already exist
      logger.warn("Error ensuring orders table (might already exist):", tableError?.message);
    }

    // Authentication required - no guest orders allowed
    let sessionUser = await getSessionUserFromRequest(request);
    
    // Log for debugging
    console.log('[POST /api/orders/create] Session check:', {
      hasSessionUser: !!sessionUser,
      userId: sessionUser?.id,
      sessionUserId: sessionUser?.id,
      sessionUserRole: sessionUser?.role,
    });
    
    // Fallback: ÿß⁄Øÿ± session Ÿæ€åÿØÿß ŸÜÿ¥ÿØ ÿßŸÖÿß userId ÿØÿ± header ÿßÿ±ÿ≥ÿßŸÑ ÿ¥ÿØŸá (ÿ®ÿ±ÿß€å development)
    if (!sessionUser && process.env.NODE_ENV === 'development') {
      const userIdHeader = request.headers.get('x-user-id');
      if (userIdHeader) {
        console.log('[POST /api/orders/create] Using userId from header (development fallback):', userIdHeader);
        const user = await getRow<{
          id: string;
          name: string;
          phone: string;
          role: string;
          enabled: any;
          createdAt: string;
        }>(
          "SELECT id, name, phone, role, enabled, createdAt FROM users WHERE id = ?",
          [userIdHeader]
        );
        if (user && user.enabled) {
          sessionUser = {
            id: user.id,
            name: user.name,
            phone: user.phone,
            role: user.role || "user",
            enabled: Boolean(user.enabled),
            createdAt: user.createdAt || new Date().toISOString(),
          };
          console.log('[POST /api/orders/create] Fallback user found:', sessionUser.id);
        }
      }
    }
    
    if (!sessionUser || !sessionUser.id) {
      throw new AppError("ÿ®ÿ±ÿß€å ÿ´ÿ®ÿ™ ÿ≥ŸÅÿßÿ±ÿ¥ ÿ®ÿß€åÿØ Ÿàÿßÿ±ÿØ ÿ≠ÿ≥ÿßÿ® ⁄©ÿßÿ±ÿ®ÿ±€å ÿÆŸàÿØ ÿ¥Ÿà€åÿØ", 401, "UNAUTHORIZED");
    }
    
    const finalUserId = sessionUser.id;
    
    logger.info(`üì¶ Creating order with userId: ${finalUserId}`);

    const body = await request.json().catch(() => {
      throw new AppError("Invalid JSON in request body", 400, "INVALID_JSON");
    });
    const {
      items,
      formData,
      total,
      shippingCost,
      shippingMethod,
    } = body;

    // ÿßÿπÿ™ÿ®ÿßÿ±ÿ≥ŸÜÿ¨€å ÿ≥ÿ®ÿØ ÿÆÿ±€åÿØ
    if (!items || !Array.isArray(items) || items.length === 0) {
      throw new AppError("ÿ≥ÿ®ÿØ ÿÆÿ±€åÿØ ÿÆÿßŸÑ€å ÿßÿ≥ÿ™", 400, "EMPTY_CART");
    }

    // Log items for debugging
    logger.debug("Received items for order creation:", items.map(item => ({
      id: item?.id,
      name: item?.name,
      price: item?.price,
      quantity: item?.quantity,
      hasImage: !!item?.image,
    })));

    // ÿ®ÿ±ÿ±ÿ≥€å ŸÖÿ≠ÿØŸàÿØ€åÿ™ ÿ™ÿπÿØÿßÿØ ŸÖÿ≠ÿµŸàŸÑÿßÿ™
    if (items.length > 50) {
      throw new AppError("ÿ≠ÿØÿß⁄©ÿ´ÿ± 50 ŸÖÿ≠ÿµŸàŸÑ ÿØÿ± Ÿáÿ± ÿ≥ŸÅÿßÿ±ÿ¥ ŸÖÿ¨ÿßÿ≤ ÿßÿ≥ÿ™", 400, "CART_LIMIT_EXCEEDED");
    }

    // ÿßÿπÿ™ÿ®ÿßÿ±ÿ≥ŸÜÿ¨€å Ÿáÿ± ÿ¢€åÿ™ŸÖ
    for (const item of items) {
      // ÿ®ÿ±ÿ±ÿ≥€å Ÿàÿ¨ŸàÿØ ŸÅ€åŸÑÿØŸáÿß€å ÿ∂ÿ±Ÿàÿ±€å
      if (!item || typeof item !== "object") {
        throw new AppError("ÿ¢€åÿ™ŸÖ ÿ≥ŸÅÿßÿ±ÿ¥ ŸÜÿßŸÖÿπÿ™ÿ®ÿ± ÿßÿ≥ÿ™", 400, "INVALID_ITEM");
      }

      if (!item.id || typeof item.id !== "string" || item.id.trim() === "") {
        throw new AppError(
          `ÿ¥ŸÜÿßÿ≥Ÿá ŸÖÿ≠ÿµŸàŸÑ ŸÜÿßŸÖÿπÿ™ÿ®ÿ± ÿßÿ≥ÿ™. ÿ¢€åÿ™ŸÖ: ${JSON.stringify(item)}`,
          400,
          "INVALID_ITEM_ID"
        );
      }

      if (!item.name || typeof item.name !== "string" || item.name.trim() === "") {
        throw new AppError(
          `ŸÜÿßŸÖ ŸÖÿ≠ÿµŸàŸÑ ŸÜÿßŸÖÿπÿ™ÿ®ÿ± ÿßÿ≥ÿ™. ÿ¥ŸÜÿßÿ≥Ÿá: ${item.id}`,
          400,
          "INVALID_ITEM_NAME"
        );
      }

      // ÿ®ÿ±ÿ±ÿ≥€å ŸÇ€åŸÖÿ™
      if (item.price === undefined || item.price === null) {
        throw new AppError(
          `ŸÇ€åŸÖÿ™ ŸÖÿ≠ÿµŸàŸÑ "${item.name || item.id}" ŸÖÿ¥ÿÆÿµ ŸÜÿ¥ÿØŸá ÿßÿ≥ÿ™`,
          400,
          "MISSING_PRICE"
        );
      }

      const price = sanitizeNumber(item.price);
      // ÿßŸÅÿ≤ÿß€åÿ¥ ŸÖÿ≠ÿØŸàÿØ€åÿ™ ŸÇ€åŸÖÿ™ ÿ®Ÿá 1 ŸÖ€åŸÑ€åÿßÿ±ÿØ ÿ™ŸàŸÖÿßŸÜ ÿ®ÿ±ÿß€å ŸÖÿ≠ÿµŸàŸÑÿßÿ™ ⁄Øÿ±ÿßŸÜ ŸÇ€åŸÖÿ™
      if (price <= 0 || price > 1000000000) {
        throw new AppError(
          `ŸÇ€åŸÖÿ™ ŸÖÿ≠ÿµŸàŸÑ "${item.name}" ŸÜÿßŸÖÿπÿ™ÿ®ÿ± ÿßÿ≥ÿ™ (${price}) - ÿ≠ÿØÿß⁄©ÿ´ÿ± ŸÇ€åŸÖÿ™ ŸÖÿ¨ÿßÿ≤: 1,000,000,000 ÿ™ŸàŸÖÿßŸÜ`,
          400,
          "INVALID_PRICE"
        );
      }

      // ÿ®ÿ±ÿ±ÿ≥€å ÿ™ÿπÿØÿßÿØ
      if (item.quantity === undefined || item.quantity === null) {
        throw new AppError(
          `ÿ™ÿπÿØÿßÿØ ŸÖÿ≠ÿµŸàŸÑ "${item.name || item.id}" ŸÖÿ¥ÿÆÿµ ŸÜÿ¥ÿØŸá ÿßÿ≥ÿ™`,
          400,
          "MISSING_QUANTITY"
        );
      }

      const quantity = sanitizeNumber(item.quantity);
      if (quantity <= 0 || quantity > 1000) {
        throw new AppError(
          `ÿ™ÿπÿØÿßÿØ ŸÖÿ≠ÿµŸàŸÑ "${item.name}" ŸÜÿßŸÖÿπÿ™ÿ®ÿ± ÿßÿ≥ÿ™ (${quantity}) - ÿ®ÿß€åÿØ ÿ®€åŸÜ 1 ÿ™ÿß 1000 ÿ®ÿßÿ¥ÿØ`,
          400,
          "INVALID_QUANTITY"
        );
      }

      // ÿ™ÿµŸà€åÿ± ÿßÿÆÿ™€åÿßÿ±€å ÿßÿ≥ÿ™ - ÿß⁄Øÿ± Ÿàÿ¨ŸàÿØ ŸÜÿØÿßÿ¥ÿ™Ÿá ÿ®ÿßÿ¥ÿØÿå ŸÖŸÇÿØÿßÿ± Ÿæ€åÿ¥‚ÄåŸÅÿ±ÿ∂ ŸÖ€å‚Äå⁄Øÿ∞ÿßÿ±€åŸÖ
      if (item.image !== undefined && item.image !== null && typeof item.image !== "string") {
        throw new AppError(
          `ÿ™ÿµŸà€åÿ± ŸÖÿ≠ÿµŸàŸÑ "${item.name}" ŸÜÿßŸÖÿπÿ™ÿ®ÿ± ÿßÿ≥ÿ™`,
          400,
          "INVALID_IMAGE"
        );
      }
    }

    // ÿßÿπÿ™ÿ®ÿßÿ±ÿ≥ŸÜÿ¨€å ŸÅÿ±ŸÖ ÿ®ÿß zod
    try {
      await checkoutFormSchema.parseAsync(formData);
    } catch (validationError: any) {
      throw new AppError(
        "ÿßÿ∑ŸÑÿßÿπÿßÿ™ ŸÅÿ±ŸÖ ŸÜÿßŸÖÿπÿ™ÿ®ÿ± ÿßÿ≥ÿ™",
        400,
        "VALIDATION_ERROR",
        validationError.errors || validationError.message
      );
    }

    // ÿßÿπÿ™ÿ®ÿßÿ±ÿ≥ŸÜÿ¨€å ŸÖÿ®ŸÑÿ∫
    const sanitizedTotal = sanitizeNumber(total);
    const sanitizedShippingCost = sanitizeNumber(shippingCost || 0);

    if (sanitizedTotal <= 0 || !isFinite(sanitizedTotal)) {
      throw new AppError("ŸÖÿ®ŸÑÿ∫ ÿ≥ŸÅÿßÿ±ÿ¥ ŸÜÿßŸÖÿπÿ™ÿ®ÿ± ÿßÿ≥ÿ™", 400, "INVALID_TOTAL");
    }

    if (sanitizedShippingCost < 0 || !isFinite(sanitizedShippingCost)) {
      throw new AppError("Ÿáÿ≤€åŸÜŸá ÿßÿ±ÿ≥ÿßŸÑ ŸÜÿßŸÖÿπÿ™ÿ®ÿ± ÿßÿ≥ÿ™", 400, "INVALID_SHIPPING_COST");
    }

    // ÿ®ÿ±ÿ±ÿ≥€å ŸÖÿ≠ÿØŸàÿØ€åÿ™ ŸÖÿ®ŸÑÿ∫ ⁄©ŸÑ
    const maxOrderAmount = 10000000000; // 10 ŸÖ€åŸÑ€åÿßÿ±ÿØ ÿ™ŸàŸÖÿßŸÜ (ÿ®ÿ±ÿß€å ÿ≥ŸÅÿßÿ±ÿ¥ÿßÿ™ ÿ®ÿ≤ÿ±⁄Ø)
    if (sanitizedTotal > maxOrderAmount) {
      throw new AppError(
        `ŸÖÿ®ŸÑÿ∫ ÿ≥ŸÅÿßÿ±ÿ¥ ŸÜŸÖ€å‚Äåÿ™ŸàÿßŸÜÿØ ÿ®€åÿ¥ÿ™ÿ± ÿßÿ≤ ${maxOrderAmount.toLocaleString("fa-IR")} ÿ™ŸàŸÖÿßŸÜ ÿ®ÿßÿ¥ÿØ`,
        400,
        "ORDER_AMOUNT_EXCEEDED"
      );
    }

    // ÿßÿπÿ™ÿ®ÿßÿ±ÿ≥ŸÜÿ¨€å ÿ±Ÿàÿ¥ ÿßÿ±ÿ≥ÿßŸÑ
    if (shippingMethod && shippingMethod !== "air" && shippingMethod !== "sea") {
      throw new AppError("ÿ±Ÿàÿ¥ ÿßÿ±ÿ≥ÿßŸÑ ŸÜÿßŸÖÿπÿ™ÿ®ÿ± ÿßÿ≥ÿ™", 400, "INVALID_SHIPPING_METHOD");
    }

    // ÿ®ÿ±ÿ±ÿ≥€å ÿß€åŸÜ⁄©Ÿá ÿ±Ÿàÿ¥ ÿßÿ±ÿ≥ÿßŸÑ ÿßŸÜÿ™ÿÆÿßÿ® ÿ¥ÿØŸá ÿ®ÿ±ÿß€å ÿ™ŸÖÿßŸÖ ŸÖÿ≠ÿµŸàŸÑÿßÿ™ ÿØÿ± ÿ≥ŸÅÿßÿ±ÿ¥ ŸÖÿπÿ™ÿ®ÿ± ÿßÿ≥ÿ™
    if (shippingMethod) {
      for (const item of items) {
        try {
          const product = await getRow<any>("SELECT \"airShippingEnabled\", \"seaShippingEnabled\" FROM products WHERE id = ?", [item.id]);
          if (product) {
            if (shippingMethod === "air" && !product.airShippingEnabled) {
              throw new AppError(
                `ÿ±Ÿàÿ¥ ÿßÿ±ÿ≥ÿßŸÑ ŸáŸàÿß€å€å ÿ®ÿ±ÿß€å ŸÖÿ≠ÿµŸàŸÑ "${item.name}" ŸÅÿπÿßŸÑ ŸÜ€åÿ≥ÿ™`,
                400,
                "SHIPPING_METHOD_NOT_AVAILABLE"
              );
            }
            if (shippingMethod === "sea" && !product.seaShippingEnabled) {
              throw new AppError(
                `ÿ±Ÿàÿ¥ ÿßÿ±ÿ≥ÿßŸÑ ÿØÿ±€åÿß€å€å ÿ®ÿ±ÿß€å ŸÖÿ≠ÿµŸàŸÑ "${item.name}" ŸÅÿπÿßŸÑ ŸÜ€åÿ≥ÿ™`,
                400,
                "SHIPPING_METHOD_NOT_AVAILABLE"
              );
            }
          }
        } catch (error: any) {
          // ÿß⁄Øÿ± ÿÆÿ∑ÿß€å AppError ÿßÿ≥ÿ™ÿå ÿ¢ŸÜ ÿ±ÿß throw ⁄©ŸÜ
          if (error instanceof AppError) {
            throw error;
          }
          // ÿØÿ± ÿ∫€åÿ± ÿß€åŸÜ ÿµŸàÿ±ÿ™ÿå ÿß⁄Øÿ± ŸÖÿ≠ÿµŸàŸÑ Ÿæ€åÿØÿß ŸÜÿ¥ÿØÿå ÿßÿØÿßŸÖŸá ÿ®ÿØŸá (ŸÖŸÖ⁄©ŸÜ ÿßÿ≥ÿ™ ŸÖÿ≠ÿµŸàŸÑ ÿ≠ÿ∞ŸÅ ÿ¥ÿØŸá ÿ®ÿßÿ¥ÿØ)
          logger.warn(`Product ${item.id} not found, skipping shipping method validation`);
        }
      }
    }

    // ÿ™ŸàŸÑ€åÿØ ÿ¥ŸÖÿßÿ±Ÿá ÿ≥ŸÅÿßÿ±ÿ¥
    const generateOrderNumber = () => {
      const timestamp = Date.now();
      const random = Math.floor(Math.random() * 1000).toString().padStart(3, "0");
      return `ORD-${timestamp}-${random}`;
    };

    // Sanitize ÿØÿßÿØŸá‚ÄåŸáÿß€å ŸÅÿ±ŸÖ
    const sanitizedFormData = {
      firstName: sanitizeString(formData.firstName),
      lastName: sanitizeString(formData.lastName || ""),
      phone: sanitizeString(formData.phone).replace(/\D/g, ""),
      email: formData.email ? sanitizeString(formData.email).toLowerCase() : undefined,
      addressType: formData.addressType || "address",
      location: formData.location ? sanitizeString(formData.location) : undefined,
      address: formData.address ? sanitizeString(formData.address) : undefined,
      city: formData.city ? sanitizeString(formData.city) : undefined,
      postalCode: formData.postalCode ? sanitizeString(formData.postalCode).replace(/\D/g, "") : undefined,
      province: formData.province ? sanitizeString(formData.province) : "",
      notes: formData.notes ? sanitizeString(formData.notes) : undefined,
    };

    // ÿ≥ÿßÿÆÿ™ shippingAddress ÿ®ÿ± ÿßÿ≥ÿßÿ≥ addressType
    let shippingAddress: any = {
      addressType: sanitizedFormData.addressType,
      province: sanitizedFormData.province || undefined,
    };

    if (sanitizedFormData.addressType === "location") {
      shippingAddress.location = sanitizedFormData.location;
    } else if (sanitizedFormData.addressType === "postalCode") {
      shippingAddress.postalCode = sanitizedFormData.postalCode;
    } else {
      // address type
      shippingAddress.address = sanitizedFormData.address;
      shippingAddress.city = sanitizedFormData.city;
      if (sanitizedFormData.postalCode) {
        shippingAddress.postalCode = sanitizedFormData.postalCode;
      }
    }

    // ÿ≥ÿßÿÆÿ™ ÿØÿßÿØŸá‚ÄåŸáÿß€å ÿ≥ŸÅÿßÿ±ÿ¥ ÿ®ÿß ÿØÿßÿØŸá‚ÄåŸáÿß€å sanitize ÿ¥ÿØŸá
    const orderData = {
      items: items.map((item: any) => ({
        id: sanitizeString(item.id),
        productId: sanitizeString(item.id),
        name: sanitizeString(item.name),
        price: sanitizeNumber(item.price),
        quantity: Math.floor(sanitizeNumber(item.quantity)),
        image: item.image ? sanitizeString(item.image) : "",
      })),
      total: Math.round(sanitizedTotal - sanitizedShippingCost),
      shippingCost: Math.round(sanitizedShippingCost),
      shippingMethod: (shippingMethod || "air") as "air" | "sea",
      status: "pending" as const,
      paymentStatus: "paid" as const,
      customerName: `${sanitizedFormData.firstName}${sanitizedFormData.lastName ? ` ${sanitizedFormData.lastName}` : ""}`.trim(),
      customerPhone: sanitizedFormData.phone,
      customerEmail: sanitizedFormData.email || undefined,
      shippingAddress,
      notes: sanitizedFormData.notes || undefined,
      userId: finalUserId,
      orderNumber: generateOrderNumber(),
    };

    // Save order to database
    const id = `order-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`;
    const now = new Date().toISOString();

    // ÿßÿ∑ŸÖ€åŸÜÿßŸÜ ÿßÿ≤ ÿß€åŸÜ⁄©Ÿá userId ÿ®Ÿá ÿµŸàÿ±ÿ™ string ÿ∞ÿÆ€åÿ±Ÿá ŸÖ€å‚Äåÿ¥ŸàÿØ
    const userIdToSave = String(finalUserId);

    // Log before insert
    logger.info(`üì¶ Attempting to insert order: ${orderData.orderNumber}`, {
      id,
      userId: userIdToSave,
      customerName: orderData.customerName,
      customerPhone: orderData.customerPhone,
      total: orderData.total,
      itemsCount: orderData.items.length,
    });

    try {
      const insertResult = await runQuery(
        `INSERT INTO orders (id, \`orderNumber\`, \`userId\`, \`customerName\`, \`customerPhone\`, \`customerEmail\`, items, total, \`shippingCost\`, \`shippingMethod\`, \`shippingAddress\`, status, \`paymentStatus\`, notes, \`createdAt\`, \`updatedAt\`)
         VALUES (?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?)`,
        [
          id,
          orderData.orderNumber,
          userIdToSave,
          orderData.customerName,
          orderData.customerPhone,
          orderData.customerEmail || null,
          JSON.stringify(orderData.items),
          orderData.total,
          orderData.shippingCost,
          orderData.shippingMethod,
          JSON.stringify(orderData.shippingAddress),
          orderData.status,
          orderData.paymentStatus,
          orderData.notes || null,
          now,
          now,
        ]
      );
      
      logger.info(`‚úÖ Order inserted successfully: ${orderData.orderNumber}`, {
        id,
        affectedRows: insertResult.changes,
        insertId: insertResult.lastInsertRowid,
      });
    } catch (insertError: any) {
      logger.error(`‚ùå Error inserting order: ${orderData.orderNumber}`, {
        id,
        error: insertError?.message,
        code: insertError?.code,
        sqlState: insertError?.sqlState,
        errno: insertError?.errno,
        stack: insertError?.stack,
      });
      
      // Re-throw with more context
      throw new AppError(
        `ÿÆÿ∑ÿß ÿØÿ± ÿ∞ÿÆ€åÿ±Ÿá ÿ≥ŸÅÿßÿ±ÿ¥ ÿØÿ± ÿØ€åÿ™ÿßÿ®€åÿ≥: ${insertError?.message || 'ÿÆÿ∑ÿß€å ŸÜÿßŸÖÿ¥ÿÆÿµ'}`,
        500,
        "ORDER_INSERT_ERROR",
        { originalError: insertError?.code, orderNumber: orderData.orderNumber }
      );
    }
    
    // Log successful order creation with userId
    logger.info(`‚úÖ Order created successfully: ${orderData.orderNumber} with userId: ${userIdToSave}`);

    // Fetch the saved order
    let savedOrder;
    try {
      savedOrder = await getRow<any>("SELECT * FROM orders WHERE id = ?", [id]);
      
      if (!savedOrder) {
        logger.error(`‚ùå Order not found after insert: ${id}`, {
          orderNumber: orderData.orderNumber,
        });
        throw new AppError("ÿ≥ŸÅÿßÿ±ÿ¥ ÿ´ÿ®ÿ™ ÿ¥ÿØ ÿßŸÖÿß ÿ®ÿßÿ≤€åÿßÿ®€å ŸÜÿ¥ÿØ", 500, "ORDER_NOT_FOUND_AFTER_INSERT");
      }
      
      logger.info(`‚úÖ Order fetched successfully: ${orderData.orderNumber}`, {
        id: savedOrder.id,
        userId: savedOrder.userId,
      });
    } catch (fetchError: any) {
      logger.error(`‚ùå Error fetching order after insert: ${id}`, {
        error: fetchError?.message,
        code: fetchError?.code,
      });
      throw new AppError(
        `ÿÆÿ∑ÿß ÿØÿ± ÿ®ÿßÿ≤€åÿßÿ®€å ÿ≥ŸÅÿßÿ±ÿ¥: ${fetchError?.message || 'ÿÆÿ∑ÿß€å ŸÜÿßŸÖÿ¥ÿÆÿµ'}`,
        500,
        "ORDER_FETCH_ERROR"
      );
    }

    // Parse JSON fields (PostgreSQL JSONB returns objects, not strings)
    const parsedOrder = {
      ...savedOrder,
      items: Array.isArray(savedOrder.items) ? savedOrder.items : (typeof savedOrder.items === 'string' ? JSON.parse(savedOrder.items) : []),
      shippingAddress: typeof savedOrder.shippingAddress === 'object' && savedOrder.shippingAddress !== null 
        ? savedOrder.shippingAddress 
        : (typeof savedOrder.shippingAddress === 'string' ? JSON.parse(savedOrder.shippingAddress) : {}),
      total: Number(savedOrder.total),
      shippingCost: Number(savedOrder.shippingCost),
      createdAt: savedOrder.createdAt instanceof Date ? savedOrder.createdAt : new Date(savedOrder.createdAt),
      updatedAt: savedOrder.updatedAt instanceof Date ? savedOrder.updatedAt : new Date(savedOrder.updatedAt),
    };

    return createSuccessResponse({
      order: parsedOrder,
    }, 201);
  } catch (error) {
    return createErrorResponse(error);
  }
}
